import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';

@Injectable({
    providedIn: "root"
})
export class UserService {
    public baseUrl = environment.production ? environment.production_url : environment.development_url

    constructor(private http: HttpClient) { }

    public async getAllUsers(): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return await this.http.get<any>(`${this.baseUrl}/api/readAllUsers`, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async createUser(user): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return await this.http.post<any>(`${this.baseUrl}/api/createUser`, user, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async updateUser(id, user): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
           return await this.http.post<any>(`${this.baseUrl}/api/updateUser/${id}`, user, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async deleteUser(id): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return await this.http.post<any>(`${this.baseUrl}/api/deleteUser/${id}`, {}, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async loginUser(user): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return await this.http.post<any>(`${this.baseUrl}/api/loginUser`, user, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async ensureAuthentication(id, token): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 
                'Content-Type': 'application/json',
                'Authentication': token
            }),
        };
        try {
            return await this.http.post<any>(`${this.baseUrl}/api/ensureAuthentication/${id}`, {}, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }

    public async resetPassword(id, passwords): Promise<any> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return await this.http.post<any>(`${this.baseUrl}/api/resetPassword/${id}`, passwords, httpOptions).toPromise();
        } catch (error) {
            if(error.status.toString().split("")[0] === "5" || error.status.toString().split("")[0] === "4") throw error;
        }
    }
}