import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../environments/environment';

@Injectable({
    providedIn: "root"
})
export class InstanceService {
    public baseUrl = environment.production? environment.production_url : environment.development_url

    constructor(private http: HttpClient) { }

    public async getAllInstances() : Promise<Array<Object>> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return this.http.get<any>(`${this.baseUrl}/api/getAllInstances`, httpOptions).toPromise();
        } catch (error) {
            console.log(error);
        }
    }

    public async getInstance(instanceId: string) : Promise<Object> {
        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
        };
        try {
            return this.http.get<any>(`${this.baseUrl}/api/getInstance/${instanceId}`, httpOptions).toPromise();
        } catch (error) {
            console.log(error);
        }
    }
}