import { Component, OnInit } from '@angular/core';
import { FormBuilder } from "@angular/forms";
import { UserService } from '../services/user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {
  public showLoading = true;
  public userList: Array<Object> = [];
  public modelCreate = false;
  public modelEdit = false;
  public modelDelete = false;
  public modelResetPassword = false;
  public user: any = {};
  public offset = { x: 0, y: 60 }

  public userForm = this.fb.group({
    name: [{ disabled: false }],
    email: [{ disabled: false }],
    password: [{ disabled: false }],
    confirmPassword: [{ disabled: false }]
  });

  public invalid = {
    name: {
      status: false,
      message: ''
    },
    email: {
      status: false,
      message: ''
    },
    password: {
      status: false,
      message: ''
    },
    confirm_password: {
      status: false,
      message: ''
    }
  };

  public notification = false;
  public notificationData = {};

  constructor(public userService: UserService, private fb: FormBuilder, private router: Router) { }

  ngOnInit(): void {
    this.showLoading = true;

    const role = localStorage.getItem('role');
    if (!role) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    } else if (role !== "admin") this.router.navigate([`/home`]);

    const token = localStorage.getItem('token');
    if (!token) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    const id = localStorage.getItem('id');
    if (!id) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    this.userService.ensureAuthentication(id, token)
      .then(data => this.userService.getAllUsers().then(users => this.userList = users))
      .catch(err => {
        localStorage.clear();
        this.router.navigate(["/login"]);
      })
    this.showLoading = false;
  }

  openModalCreate() {
    this.userForm.patchValue({
      name: "",
      email: "",
      password: "",
      confirmPassword: ""
    })
    this.modelCreate = true;
  }

  openModalEdit(user) {
    this.userForm.patchValue({
      name: user.name,
      email: user.email
    })
    this.user.id = user.id;
    this.user.role = user.role;
    this.modelEdit = true;
  }

  openModalDelete(user) {
    this.user = user;
    this.modelDelete = true;
  }

  openModalresetPassword(user) {
    this.userForm.patchValue({
      password: "",
      confirmPassword: ""
    })

    this.user = user;
    this.modelResetPassword = true;
  }

  closeModalDelete() {
    this.modelDelete = false;
  }

  closeModalEdit() {
    this.modelEdit = false;
  }

  closeModalCreate() {
    this.modelCreate = false;
  }

  closeModalResetPassword() {
    this.modelResetPassword = false;
  }

  createUser(role) {
    const isValid = this.validateFields("create");

    if (isValid) {
      let newUser = {
        name: this.userForm.value.name,
        email: this.userForm.value.email,
        role: role.value ? role.value : "user",
        password: this.userForm.value.password,
      }

      this.userService.createUser(newUser)
        .then(data => {
          this.notificationData = {
            type: 'success',
            title: 'Criação de usuário',
            subtitle: 'Usuáro criado com sucesso.',
            lowContrast: true,
            showClose: false
          }
          this.notification = true;
          
          this.sleep(3000).then(() => {
            this.notification = false;
            this.closeModalCreate();
            this.refreshData();
          });

        }).catch(error => {
          this.notificationData = {
            type: 'error',
            title: error.status.toString().split("")[0] === "5" || error.status === 400 ? 'Erro interno' : 'Erro ao criar usuário',
            subtitle: error.status === 406 ? error.error : 'Não foi possível criar um novo usuário. Tente novamente mais tarde.',
            lowContrast: true,
            showClose: false
          }
          this.notification = true;

          this.sleep(5000).then(() => {
            this.notification = false;
          });
        })
    }
  }

  updateUser(role) {
    const isValid = this.validateFields("edit");

    if (isValid) {
      let newUser = {
        name: this.userForm.value.name,
        email: this.userForm.value.email,
        role: role.value ? role.value : this.user.role
      }
      this.userService.updateUser(this.user.id, newUser)
        .then(data => {
          this.notification = true;
          this.notificationData = {
            type: 'success',
            title: 'Edição de usuário',
            subtitle: 'Usuáro editado com sucesso.',
            lowContrast: true,
            showClose: false
          }

          this.sleep(3000).then(() => {
            this.notification = false;
            this.closeModalEdit();
            this.refreshData();
          });
        }).catch(error => {
          this.notificationData = {
            type: 'error',
            title: error.status.toString().split("")[0] === "5" || error.status === 400 ? 'Erro interno' : 'Erro ao editar usuário',
            subtitle: error.status === 404 ? error.error : 'Não foi possível editar o usuário. Tente novamente mais tarde.',
            lowContrast: true,
            showClose: false
          }
          this.notification = true;
  
          this.sleep(5000).then(() => {
            this.notification = false;
          });

          this.closeModalEdit();
        })
    }
  }

  deleteUser(userId) {
    this.userService.deleteUser(userId)
      .then(data => {
        this.notificationData = {
          type: 'success',
          title: 'Deleção de usuário',
          subtitle: 'Usuáro deletado com sucesso.',
          lowContrast: true,
          showClose: false
        }
        this.notification = true;
        
        this.sleep(3000).then(() => {
          this.notification = false;
          this.closeModalDelete();
          this.refreshData();
        });
      }).catch(error => {
        this.notificationData = {
          type: 'error',
          title: error.status.toString().split("")[0] === "5" || error.status === 400 ? 'Erro interno' : 'Erro ao deletar usuário',
          subtitle: error.status === 404 ? error.error : 'Não foi possível deletar o usuário. Tente novamente mais tarde.',
          lowContrast: true,
          showClose: false
        }
        this.notification = true;

        this.sleep(5000).then(() => {
          this.notification = false;
        });
        
        this.closeModalDelete();
        this.refreshData();
      })
  }

  resetPassword() {
    const isValid = this.validateFields("resetPassword");

    if (isValid) {
      const id = this.user.id;
      const body = {
        new_password: this.userForm.value.password,
        new_password_confirm: this.userForm.value.confirmPassword,
        action: "admin"
      }

      this.userService.resetPassword(id, body)
        .then(data => {
          this.notification = true;
          this.notificationData = {
            type: 'success',
            title: 'Alteração de senha',
            subtitle: 'Senha alterada com sucesso.',
            lowContrast: true,
            showClose: false
          }
          
          this.sleep(3000).then(() => {
            this.notification = false;
            this.closeModalResetPassword();
            this.router.navigate(["/users"]);
          });
          
        }).catch(error => {
          this.notification = true;
          this.notificationData = {
            type: 'error',
            title: error.status.toString().split("")[0] === "5" || error.status === 400 ? 'Erro interno' : 'Erro ao alterar senha',
            subtitle: error.error ? error.error : 'Não foi possível atualizar a senha. Tente novamente mais tarde.',
            lowContrast: true,
            showClose: false
          }

          this.sleep(7000).then(() => {
            this.notification = false;
          });
        });
    }
  }

  refreshData() {
    location.reload();
  }

  validateFields(type): boolean {
    let status: boolean = this.userForm.valid;

    this.invalid = {
      name: {
        status: false,
        message: ''
      },
      email: {
        status: false,
        message: ''
      },
      password: {
        status: false,
        message: ''
      },
      confirm_password: {
        status: false,
        message: ''
      }
    };

    if (type === "create" || type === "edit") {
      if (!this.userForm.controls.name.valid) {
        this.invalid.name.status = true;
        this.invalid.name.message = "Campo obrigatório";
      }

      if (!this.userForm.controls.email.valid) {
        this.invalid.email.status = true;
        this.invalid.email.message = "Campo obrigatório";
      } else if (!this.userForm.value.email.match(/^[a-z0-9.]+@[a-z0-9]+\.[a-z]+(\.[a-z]+)?$/i)) {
        this.invalid.email.status = true;
        this.invalid.email.message = "O campo precisa ser um e-mail válido";
        status = false;
      }
    }

    if (type === "create" || type === "resetPassword") {
      if (!this.userForm.controls.password.valid) {
        this.invalid.password.status = true;
        this.invalid.password.message = "Campo obrigatório";
      }

      if (!this.userForm.controls.confirmPassword.valid) {
        this.invalid.confirm_password.status = true;
        this.invalid.confirm_password.message = "Campo obrigatório";
      }

      if (this.userForm.value.password !== this.userForm.value.confirmPassword) {
        this.invalid.confirm_password.status = true;
        this.invalid.confirm_password.message = "As senhas não correspondem";
        status = false;
      }
    }

    return status;
  }

  sleep(time) {
    return new Promise((resolve) => setTimeout(resolve, time));
  }
}