import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder } from "@angular/forms";
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public loginForm = this.fb.group({
    email: [{ value: "", disabled: false }],
    password: [{ value: "", disabled: false }]
  });

  public invalid = {
    email: {
      status: false,
      message: ''
    },
    password: {
      status: false,
      message: ''
    }
  };

  public notification = false;
  public notificationData = {};

  constructor(private fb: FormBuilder, public userService: UserService, private router: Router) { }

  ngOnInit(): void {
    const id = localStorage.getItem('id');
    const token = localStorage.getItem('token');

    if (id && token) {
      this.userService.ensureAuthentication(id, token)
        .then(data => {
          this.router.navigate(["/home"]);
        })
        .catch(err => {
          localStorage.clear();
        })
    }
  }

  loginUser(): void {
    const isValid = this.validateFields();

    if (isValid) {
      let user = {
        email: this.loginForm.value.email,
        password: this.loginForm.value.password,
      }

      this.userService.loginUser(user)
        .then(data => {
          localStorage.setItem('id', data.id);
          localStorage.setItem('token', data.token);
          localStorage.setItem('role', data.role);
          this.router.navigate([`/`]);
        }).catch(error => {
          if (error.status === 404) {
            this.notification = true;
            this.notificationData = {
              type: 'error',
              title: 'Erro de Log In',
              subtitle: 'Usuário ou senha não correspondem.',
              lowContrast: true,
              showClose: false
            }
          } else {
            this.notification = true;
            this.notificationData = {
              type: 'error',
              title: 'Erro interno',
              subtitle: 'Não foi possível realizar o Log In. Tente novamente mais tarde.',
              lowContrast: true,
              showClose: false
            }
          }

          this.sleep(7000).then(() => {
            this.notification = false;
          });

        });
    }
  }

  validateFields(): boolean {
    let status: boolean = this.loginForm.valid;

    this.invalid = {
      email: {
        status: false,
        message: ''
      },
      password: {
        status: false,
        message: ''
      }
    };

    if (!this.loginForm.controls.password.valid) {
      this.invalid.password.status = true;
      this.invalid.password.message = "Campo obrigatório";
    }

    if (!this.loginForm.controls.email.valid) {
      this.invalid.email.status = true;
      this.invalid.email.message = "Campo obrigatório";
    } else if (!this.loginForm.value.email.match(/^[a-z0-9.]+@[a-z0-9]+\.[a-z]+(\.[a-z]+)?$/i)) {
      this.invalid.email.status = true;
      this.invalid.email.message = "O campo precisa ser um e-mail válido";
      status = false;
    }

    return status;
  }

  sleep(time) {
    return new Promise((resolve) => setTimeout(resolve, time));
  }
}
