import { Component, OnInit } from '@angular/core';
import { FormBuilder } from "@angular/forms";
import { Router } from '@angular/router';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
  public resetPasswordForm = this.fb.group({
    oldPassword: [{ value: "", disabled: false }],
    newPassword: [{ value: "", disabled: false }],
    newPasswordConfirm: [{ value: "", disabled: false }]
  });

  public invalid = {
    old_password: {
      status: false,
      message: ''
    },
    new_password: {
      status: false,
      message: ''
    },
    new_password_confirm: {
      status: false,
      message: ''
    }
  };

  public notification = false;
  public notificationData = {};

  constructor(private fb: FormBuilder, private router: Router, public userService: UserService) { }

  ngOnInit(): void {
    const token = localStorage.getItem('token');
    if (!token) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    const id = localStorage.getItem('id');
    if (!id) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    this.userService.ensureAuthentication(id, token)
      .then(data => { })
      .catch(err => {
        localStorage.clear();
        this.router.navigate(["/login"]);
      })
  }

  resetPassword(): void {
    const isValid = this.validateFields();

    if (isValid) {
      const id = localStorage.getItem('id');
      const body = {
        old_password: this.resetPasswordForm.value.oldPassword,
        new_password: this.resetPasswordForm.value.newPassword,
        new_password_confirm: this.resetPasswordForm.value.newPasswordConfirm,
        action: "user"
      }

      this.userService.resetPassword(id, body)
        .then(data => {
          this.notification = true;
          this.notificationData = {
            type: 'success',
            title: 'Alteração de senha',
            subtitle: 'Senha alterada com sucesso.',
            lowContrast: true,
            showClose: false
          }
          
          this.sleep(3000).then(() => {
            this.notification = false;
            this.router.navigate(["/home"])
          });
          
        }).catch(error => {
          this.notification = true;
          this.notificationData = {
            type: 'error',
            title: error.status.toString().split("")[0] === "5" || error.status === 400 ? 'Erro interno' : 'Erro ao alterar senha',
            subtitle: error.error ? error.error : 'Não foi possível atualizar a senha. Tente novamente mais tarde.',
            lowContrast: true,
            showClose: false
          }

          this.sleep(7000).then(() => {
            this.notification = false;
          });
        });
    }
  }

  backHome(): void {
    this.router.navigate(["/home"]);
  }

  validateFields(): boolean {
    let status: boolean = this.resetPasswordForm.valid;

    this.invalid = {
      old_password: {
        status: false,
        message: ''
      },
      new_password: {
        status: false,
        message: ''
      },
      new_password_confirm: {
        status: false,
        message: ''
      }
    };

    if (!this.resetPasswordForm.valid) {
      if (!this.resetPasswordForm.controls.oldPassword.valid) {
        this.invalid.old_password.status = true;
        this.invalid.old_password.message = "Campo obrigatório";
      }

      if (!this.resetPasswordForm.controls.newPassword.valid) {
        this.invalid.new_password.status = true;
        this.invalid.new_password.message = "Campo obrigatório";
      }

      if (!this.resetPasswordForm.controls.newPasswordConfirm.valid) {
        this.invalid.new_password_confirm.status = true;
        this.invalid.new_password_confirm.message = "Campo obrigatório";
      }
    }

    if (this.resetPasswordForm.value.newPassword !== this.resetPasswordForm.value.newPasswordConfirm) {
      this.invalid.new_password_confirm.status = true;
      this.invalid.new_password_confirm.message = "As senhas não correspondem";
      status = false;
    }

    return status
  }

  sleep(time) {
    return new Promise((resolve) => setTimeout(resolve, time));
  }
}
