import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { InstanceService } from '../services/instance.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit {
  public pvmList: Array<Object> = [];
  public showLoading = true;
  
  constructor(private router: Router, public instanceService: InstanceService, public userService: UserService) { }

  ngOnInit() {
    this.showLoading = true;

    const token = localStorage.getItem('token');
    if(!token) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    const id = localStorage.getItem('id');
    if(!id) {
      localStorage.clear();
      this.router.navigate([`/login`]);
    }

    this.userService.ensureAuthentication(id, token)
      .then(data => this.instanceService.getAllInstances().then(list => this.pvmList = list))
      .catch(err => {
        localStorage.clear();
        this.router.navigate(["/login"]);
      })
    this.showLoading = false;
  }

  pvmInstance(instanceId: string) {
    this.router.navigate(["/instance", instanceId]);
  }
}
